export default {
	data() {
		return {
			title: 'radio',
			items: [{
					value: '0',
					name: '0首付'
				},
				{
					value: '5',
					name: '5万',
					checked: 'true'
				},
				{
					value: '10',
					name: '10万'
				}
			],
			type: [{
					value:'myself',
					name:'自己持有'
				},
				{
					value:'other',
					name:'代为持有'
				},
			],
			current: 0
		}
	},
	methods: {
		radioChange(evt) {
			for (let i = 0; i < this.items.length; i++) {
				if (this.items[i].value === evt.target.value) {
					this.current = i;
					break;
				}
			}
		}
	}
}
